.menuButton {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;

  &::after {
    margin-left: 5px;
    content: '\25BE';
  }

  &:hover {
    color: var(--toobarButtonHoverColor);
  }
}

.isDisabled {
  color: var(--disabledColor);

  pointer-events: none;
}
